<template>
    <header class="header">
        <div class="container clearfix">
            <figure class="fl">
                <a href="">
                    <img src="../../../static/images/logo.jpg" alt="">
                </a>
            </figure>
            <nav class="fl">
                <ul class="clearfix nav">
                    <li v-for="(item, index) in cnav" :key="index">
                        <a href="" :class="{'active': index == cnav.length - 1}">
                            {{ item.name }}
                            <img :src="item.icon" alt="" v-if="item.icon">
                        </a>
                        <i v-if="item.sub" class="triangle"></i>
                        <ul v-if="item.sub" class="navsub clearfix">
                            <li v-for="(val, idx) in item.sub" :key="idx">
                                <a href="">{{ val }}</a>
                            </li>
                        </ul>
                    </li>
                </ul>
            </nav>
            <nav class="fr">
                <ul class="clearfix nav">
                    <li v-for="(item, index) in rnav" :key="index">
                        <a href="">
                            <img :src="item.icon" alt="" v-if="item.icon">
                            {{ item.name }}
                        </a>
                    </li>
                    <li>
                        <a href="" class="bcb"></a>
                    </li>
                </ul>
            </nav>
        </div>
    </header>
</template>

<script>
import $ from 'jquery'
export default {
    data() {
        return {
            cnav: [
                {
                    name: '',
                    sub: ['', '', '', '', '', '', '', '', '', '', '', '', '', '']
                },
                {
                    name: '24'
                },
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: ''
                },
                {
                    name: 'Blog'
                },
                {
                    name: '',
                    icon: require('../../../static/images/face.png')
                }
            ],
            rnav: [
                {
                    name: 'APP',
                    icon: require('../../../static/images/phone.png')
                },
                {
                    name: '',
                    icon: require('../../../static/images/search.png')
                },
                {
                    name: ''
                },
                {
                    name: ''
                }
            ]
        }
    },
    methods: {

    },
    mounted() {
        this.$nextTick(() => {
            $(window).scroll(() => {
                var distance = $(window).scrollTop();
                if (distance > 0) {
                    $('.header').addClass('active');
                } else {
                    $('.header').removeClass('active');
                }
            })
        })
    }
}
</script>

<style lang="less">
@import '.././../../static/less/header.less';
</style>



